.mfp-bg {
  background-color: rgba(0, 0, 0, 0.4);
  @media(max-width: 412px) {
    height: 100vh;
  }

  &.mfp-ready {
    &.authorization {
      background-color: $body-bg-color;
      opacity: 1;
    }

    &.modal-inside {
      background-color: $white;
      opacity: 1;
      @media(max-width: 412px) {
        //height: 100vh;
      }
    }
  }

  &.card-modal {
    background-color: rgba(0, 0, 0, 0.6);
    opacity: 1;
  }

  &.mfp-price {
    background-color: rgba(0, 0, 0, 0.6);
    opacity: 1;

    .mfp-close {
      z-index: 999999;
      right: 24px;
      top: 24px;
      width: 24px;
      height: 24px;
      text-indent: -999px;
      overflow: hidden;
      opacity: 1;
