  .card-footer {
    position: fixed;
    z-index: 9999;
    right: 20px;
    top: 50%;
    transform: translateY(-50%);
    width: auto;
    @media (max-width: 412px) {
      right: 50%;
      transform: translateX(50%);
      top: auto;
      bottom: 30px;
      padding-right: 0;
    }

    &-link {
      flex-direction: column;
      padding: 0;
      width: auto;
      @media (max-width: 412px) {
        flex-direction: row;
      }

      li {
        position: relative;
        @media(max-width: 412px) {
          margin: 0 5px;
        }
      }


      a {
        width: 32px;
        height: 32px;
        margin-bottom: 8px;
        background-color: rgba(153, 153, 153, 1.0);
        opacity: 1;
        border-radius: 50%;

        &:hover {
          opacity: 1;
          background-color: rgba(102, 102, 102, 1.0);
        }
      }

      .company-modal {
        right: calc(100% + 8px);
        bottom: auto;
        top: 50%;
        transform: translateY(-50%);
        @media (max-width: 412px) {
          right: auto;
          left: 50%;
          transform: translateX(-50%) translateY(0);
          top: auto;
          bottom: calc(100% + 20px)
        }
      }

      a[data-hint]:before {
        top: 50%;
        transform: translateY(-50%);
        right: calc(100% + 8px);
        left: auto;
        @media(max-width: 412px){
          top: 100%;
          transform: translateY(0) translateX(-50%);
          left: 50%;
          right: auto;
        }
      }

