.modal {
  //position: relative;
  display: block;
  background-color: $white;

  &-price {
    width: 464px;
    height: 272px;
    border-radius: 4px;
    background-color: $white;
    margin: auto;
    padding: 48px;
    box-sizing: border-box;

    &--title {
      font-size: rem(22px);
      font-weight: $font-bold;
      line-height: 1.27;
      text-align: center;
      color: $black--text;
      margin-bottom: 16px;
    }

    &--body {
      display: flex;
      align-items: flex-start;
      justify-content: space-between;

      &__wrap {
        flex-wrap: wrap;
        justify-content: center;
      }

      p {
        flex-shrink: 0;
        flex-grow: 0;
        width: 304px;
        font-size: rem(14px);
        line-height: 1.43;
        text-align: center;
        color: $black--text;
        margin-bottom: 24px;
      }

      .btn-download {
        width: 304px;
        height: 48px;
        padding: 8px 0;
        display: flex;
        align-items: center;
        justify-content: center;

        .icon-download {

        }
      }
    }

    &--item {
      width: 184px;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;

      &__icon {
        display: block;
        width: 104px;
        height: 104px;
        margin-bottom: 4px;
        transition: .2s ease all;

        img {
          display: block;
          width: 100%;
        }
      }

      &__text {
        font-size: rem(14px);
        line-height: 1.43;
        text-align: center;
        color: $black--text;
        transition: .2s ease all;
      }

      &:last-of-type {
        border-left: 1px solid rgb(230, 230, 230);
      }

      &:hover {
        text-decoration: none;

        .modal-price--item__icon {
          transform: translateY(-4px);
        }

        .modal-price--item__text {
          color: $mid-gray--text;
        }
      }

      &__disable {
        position: relative;

        .modal-price--item__icon {
          opacity: .5;
        }

        .modal-price--item__text {
          color: rgba(0, 0, 0, .2);
        }

        &:after {
          position: absolute;
          content: attr(data-hint);
          width: 190px;
          height: 56px;
          border-radius: 4px;
          background-color: rgb(153, 153, 153);
          padding: 12px;
          box-sizing: border-box;
          font-size: rem(14px);
          line-height: 1.14;
          color: $white;
          left: 151px;
          top: 50%;
          transform: translateY(-50%);
          opacity: 0;
          transition: .2s ease all;
        }

        &:before {
          content: '';
          position: absolute;
          width: 8px;
          height: 16px;
          border: 8px solid transparent;
          border-right-color: rgb(153, 153, 153);
          box-sizing: border-box;
          top: 50%;
          transform: translateY(-50%);
          left: 137px;
          opacity: 0;
          transition: .2s ease all;
        }

        &:hover {
          .modal-price--item__icon {
            transform: translateY(0);
          }

