        text-align: left;
        font-size: rem(16px);
        line-height: 1.25;
      }
    }

    .options-list {
      label {
        position: relative;
        display: inline-block;
        float: left;
        width: 140px;
        margin-right: 24px;
        margin-bottom: 24px;
        @media(max-width: 412px) {
          display: block;
          float: none;
          width: 100%;
          margin-right: 0;
        }

        &:nth-child(even) {
          margin-right: 0;
        }

        input {
          position: absolute;

          &:checked + .info:before {
            visibility: visible;
          }
        }

        .info {
          cursor: pointer;
          position: relative;
          display: block;
          padding-left: 28px;
          font-size: rem(14px);
          line-height: 1.14;
          @media(max-width: 412px) {
            font-size: 16px;
          }

          &:after {
            position: absolute;
            left: 0;
            top: 0;
            content: '';
            width: 16px;
            height: 16px;
            border: 1px solid $mid-gray--text;
            border-radius: 4px;
            box-sizing: border-box;
            background-color: $white;
            z-index: 2;
            cursor: pointer;

            &:hover {
              background-color: rgb(243, 243, 243)
            }
          }

          &:before {
            z-index: 3;
            visibility: hidden;
            content: '';
            position: absolute;
            left: 3px;
            top: 3px;
            width: 8px;
            height: 5px;
            border-bottom: 2px solid $black--text;
            border-left: 2px solid $black--text;
            transform: rotate(-45deg);
          }
        }
      }
    }

    .notifications {
      margin-top: 15px;
      border-top: 1px solid rgba(0, 0, 0, 0.1);
      padding-top: 32px;
      margin-bottom: 32px;

      .search-info--switch {
        padding-left: 20px;
      }

      .switch {

        &_input,
        &_label {
          position: absolute;
          cursor: pointer;
          left: 0;
          top: 0;
        }

        &_input {
          margin: 0;
          padding: 0;
          opacity: 0;
          height: 0;
          width: 0;
          pointer-events: none;

          &:checked + .switch_label + .switch_marker {
            left: calc(100% - 12px);
            background: $black;
            border-color: $black;
          }

          &:checked + .switch_label:after {
            color: $black--text;
          }

          &:focus + .switch_label,
          &:active + .switch_label {

          }
        }

        &_label {
          width: 24px;
          height: 8px;
          border-radius: 8px;
          background-color: rgba(0, 0, 0, 0.15);
          user-select: none;
          z-index: 1;
          transition: background .2s ease;
          color: transparent;

          &:after {
            top: 50%;
            transform: translateY(-50%);
            position: absolute;
            content: attr(data-name);
            font: {
              size: 1.4rem;
              weight: $font-regular;
            }
            line-height: 1.4rem;
            letter-spacing: normal;
            color: $black--text;
            cursor: pointer;
            background: none;
            border: none;
            white-space: nowrap;
            left: 140%;
            transition: color .2s ease;
          }
        }

        &_marker {
          position: absolute;
          top: -4px;
          left: -4px;
          width: 16px;
          height: 16px;
          border: solid 1px rgba(0, 0, 0, 0.4);
          background-color: rgb(255, 255, 255);
          border-radius: 50%;
          z-index: 2;
          pointer-events: none;
          transition: left .2s ease;
          will-change: left;
        }
      }
    }

    .btn {
      display: block;
      width: 100%;

      &-disable {
        color: $white;
      }
    }
  }

