          margin-bottom: 24px;
          overflow: auto;
          width: auto;
        }

        &-header {
          width: auto;
          @media(max-width: 412px) {
            width: 784px;
          }

          .table-col {
            font-size: 1.3rem;
            @media(min-width: 1344px) {
              font-size: 1.4rem;
            }
          }
        }

        &-row {
          width: auto;
          @media(max-width: 412px) {
            width: 784px;
          }

          &.title {

            .table-col {
              width: 100%;

              &.no-uppercase {
                text-transform: none;
              }
