            margin-right: 0;
          }

          @media (min-width: 1264px) {
            width: 112px;
            margin-right: 8px;
          }

          &.name {
            width: 140px;
            text-align: left;
            justify-content: flex-start;
            @media (min-width: 1264px) {
              width: 144px;
              margin-right: 24px;
            }
          }

          &.code {
            width: 32px;
            @media (min-width: 1264px) {
              width: 36px;
              margin-right: 24px;
            }
          }

          &.big {
            width: 124px;
            @media (min-width: 1264px) {
              width: 140px;
              margin-right: 8px;
            }
          }

          &.not-data {
            color: $mid-gray--text;
          }
