            font-size: 1.3rem;
            @media(min-width: 1184px) {
              margin-right: 144px;
            }
            @media(min-width: 1344px) {
              font-size: 1.4rem;
            }

            &.name {
              text-align: left;
              width: 472px;
              justify-content: flex-start;
              @media(min-width: 1264px) {
