              }
              @media(min-width: 1344px) {
                width: 632px;
              }
            }

            &.code {
              max-width: 48px;
              text-align: right;
              justify-content: flex-end;
            }

            &:last-of-type {
              margin-right: 0;
              max-width: 136px;
              text-align: right;
              justify-content: flex-end;
            }
          }
        }

        .table-body {
          .table-row {
            &.title {
              .table-col {
                width: 100%;
                max-width: none;
                text-align: left;
                justify-content: flex-start;
                @media(min-width: 1264px) {
                  width: 100%;
                  text-align: left;
                  justify-content: flex-start;
                }
                @media(min-width: 1344px) {
                  width: 100%;
                  text-align: left;
                  justify-content: flex-start;
                }
              }
            }
          }

          .table-col {
            font: {
              size: 1.4rem;
              weight: $font-regular;
            }
            line-height: 2rem;
            color: $black--text;
            margin-right: 0;
            max-width: 136px;
            text-align: right;
            justify-content: flex-end;
            @media(min-width: 1184px) {
              margin-right: 144px;
            }
            @media(min-width: 1344px) {
              font-size: 1.4rem;
            }

            &.code {
              max-width: 48px;
              margin-right: 64px;
            }

            &.name {
              text-align: left;
              max-width: 472px;
              justify-content: flex-start;
              margin-right: 64px;
              @media(min-width: 1264px) {
                max-width: 552px;
              }
              @media(min-width: 1344px) {
                max-width: 632px;
              }
            }

            &:last-of-type {
              margin-right: 0;
              max-width: 136px;
              text-align: right;
              justify-content: flex-end;
            }
          }
        }
      }
    }

    //---------------
    .table {
      margin-bottom: 60px;
      @media(max-width: 412px) {
        margin-bottom: 24px;
        overflow: auto;
        width: auto;
      }

      .title {
        width: 100%;
        margin-bottom: 12px;
        border-bottom: 0;
      }

      .table-header {
        margin-bottom: 0;
        @media(max-width: 412px) {
          width: 784px;
        }

        .table-col {
          justify-content: flex-end;
          text-align: right;

          &.name {
            justify-content: flex-start;
            text-align: left;
          }
        }
      }

      .table-row {
        padding: 12px 0 11px;
        border-bottom: 1px solid $light-gray;

        &.last-row {
          border-bottom: 0;
          margin-bottom: 12px;
        }

        &.title {
          .table-col {
            max-width: none;
            justify-content: flex-start;
            text-align: left;
            font-size: 1.4rem;
            font-weight: $font-bold;
            line-height: 2rem;
            letter-spacing: 0.5px;
            color: $black--text;
            text-transform: uppercase;
            @media(min-width: 1344px) {
              font-size: 1.6rem;
            }

            &.no-uppercase {
              text-transform: none;
            }
          }

        }
      }

      .table-body {
        @media(max-width: 412px) {
          width: 784px;
        }

        .table-row {
          &:not(.title) {
            cursor: pointer;

            &:hover {
              margin-right: -16px;
              margin-left: -16px;
              padding-left: 16px;
              padding-right: 16px;
              background-color: rgba(0, 0, 0, 0.05);
              border-radius: 4px;
              border-bottom-color: rgba(0, 0, 0, 0.05);
